            {{ todo.title }}
            <button class="del" @click="$emit('deleteItem',todo.id)">x</button>
        </p>
        <!-- <p>
            <input type="checkbox" name="" id="" @change="markComplete">
            {{todo.title}}
            <button class="del" @click="del">x</button>
        </p> -->
    </div>
</template>

