<template>
  <div id="app">
    <!-- header -->
    <!-- <header>代办事项</header> -->
    <Header/>
    <h2>{{ reversedMessage }} {{ now }} {{team.members}}</h2>
    <h3>{{team.teamSummary()}}</h3>
    <!-- <h2>现在时间——{{ now }}<h2/> -->
    <Todos :T="todos" @handleDelete="handleDelete"/>
  </div>
</template>
<script>

import Todos from "./components/T";
import TodoItem from "./components/TodoItem"
import Header from "./components/layout/Header"
export default {
  name:"app",
  data(){
    return {
      msg:"hello",
      message:"Helloaejoddsaassk",
      team:{
        members:["Henry","Elyse"],
        teamName:'es6',
        teamSummary:function(){
          let self=this;
          return this.members.map(function(member){
            return `${member}隶属于${self.teamName}小组`;
          })
        }
      },
      // todo:none,
      todos:[
        {
          id:1,
          title:"待办事项1",
          completed:false
        },

        {
          id:2,
          title:"待办事项2",
          completed:false
        },
        {
          id:3,
          title:"待办事项3",
          completed:false
        },
      ]
    }
  },
  components:{
    Todos,
    TodoItem,
    Header
  },
  methods:{
    handleDelete(id){
      // console.log(id);
      // console.log(this.todos.filter(todo => todo.id !== id));
      this.todos = this.todos.filter(todo => todo.id !== id);
    }
  },
  computed:{
    now:function(){
      return Date.now()
      },
    reversedMessage: function () {
      // `this` 指向 vm 实例
      return this.message.split('').reverse().join('')
    }
    },
}
</script>

